body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  flex-direction: column;
  align-items: center;
  align-content: center;
  min-width: 20%;
}

.bottomImage {
  position: absolute;
  left: 0;
  bottom: 0;
}
@-webkit-keyframes mainCoverTransition {
  from {
    background-image: url(/Reckless_Racoon_Fan_Web/static/media/aboutTheme1.5848fa1e.png);
  }
  to {
    background-image: url(/Reckless_Racoon_Fan_Web/static/media/aboutTheme2.72ab4aa1.png);
  }
}
@keyframes mainCoverTransition {
  from {
    background-image: url(/Reckless_Racoon_Fan_Web/static/media/aboutTheme1.5848fa1e.png);
  }
  to {
    background-image: url(/Reckless_Racoon_Fan_Web/static/media/aboutTheme2.72ab4aa1.png);
  }
}

.Main-cover {
  /*animation-direction: alternate-reverse;
  animation-duration: 0.7s;

   height: 1000px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */
  position: absolute;
  left: 0;
  bottom: 0;
}
@font-face {
  font-family: "NormalText";
  src: local("NormalText"), url(/Reckless_Racoon_Fan_Web/static/media/franken.9c2a5a0a.otf) format("opentype");
}
@font-face {
  font-family: "TitleText";
  src: local("TitleText"), url(/Reckless_Racoon_Fan_Web/static/media/forever.34f9dff9.otf) format("opentype");
}
.Faq {
  width: 48%;
}
.About-theme {
  background-image: linear-gradient(
      to bottom,
      #36548300,
      #f2f3f3fd
    ),
    url(/Reckless_Racoon_Fan_Web/static/media/aboutTheme1.5848fa1e.png);

  height: 1000px;

  align-items: center;
  align-content: center;
  
}
.About-collection {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 80%;
}
.Team-list {
  max-width: 45%;
}
.About-image {
  width: 40%;
  border-radius: 5%;
}
.Self-middle {
  flex-direction: row;
}
.Statistic {
    width: 80%;

}
.Sneak-peeks {
    width: 80%;

}
.Section {
/*   padding-left: 250px;
  padding-right: 250px; */
  padding-bottom: 200px;
  align-content: center;
  align-items: center;
  margin: auto;
  
}

.Main-image {
  min-height: 100vh;
  width: 100%;
  flex: 1 1;
}

.card-container {
  flex-direction: "row";
  width: 200;
  height: 300;
}

